<template>
	<div class="dingMi-head">
		<img src="../../../static/img/back.png" alt="" @click="goBack();">
		<span>钉钉小秘书</span>
	</div>
</template>
<script>
	export default{
		methods: {
			goBack: function(){
				window.history.back();
			}
		}
	}
</script>
<style lang='scss' scoped>
	.dingMi-head{
		height:50px;
		line-height:49px;
		border-bottom:1px solid #DADADE;
		position:relative;
		img{
			height:15px;
			position:absolute;
			top:16px;	
			left:3%;	
		}
		span{
			display:block;
			width:100%;
			height:100%;
			text-align: center;
			font-size:17px;
		}
	}
</style>


