<template>
	<div class="dingMi">
		<second-head></second-head>
		<div class="dingMi-content">
			<router-link to="/dingVersion" class="dingMi-cont-list">
				<img src="../../../static/img/dingMi.png">
				<dl>
					<dt>{{ msgTit }}</dt>
					<dd>{{ msgText }}</dd>
				</dl>
				<p class="lookDetails">
					<span>查看详情</span>
					<span>></span>
				</p>
			</router-link>
		</div>
		<div class="dingMi-footer">
			<div class="clickBlock" :class="{ 'show1': isA }" @click="toggleFooter"></div>
			<div class="checkFooterDiv">
				<dingMi-nav :class="{ showOne: isShow1 }"></dingMi-nav>
				<dingMi-navtwo :class="{ showTwo: isShow2 }"></dingMi-navtwo>
			</div>

		</div>
	</div>
</template>
<script>
	import secondHead from '../../components/main/secondHead.vue';
	import dingMiNav from '../../components/main/dingMi_nav.vue';
	import dingMiNavtwo from '../../components/main/dingMi_import.vue'
	export default{
		data: function(){
			return {
				msgTit: "钉钉3.4版本新功能介绍",
				msgText: "不同消息可设置不同铃声，避免遗漏重要消息，如普通消息、DING消息、@我的消息、红包消息等",
				isA: false,
				isShow1: true,
				isShow2: false
			}
		},
		components: {
			secondHead,dingMiNav,dingMiNavtwo
		},
		methods: {
			toggleFooter: function(){
				this.isA = !this.isA;
				if(this.isA = !this.isA){
					this.isShow1 == this.isShow1
					this.isShow2 == this.isShow2
				}else{
					this.isShow1 = !this.isShow1
					this.isShow2 = !this.isShow2
				}
			}

		}
	}
</script>
<style lang='less' scoped>
	.dingMi{
		width:100%;
		height:100%;

		.dingMi-content{
			width:90%;
			margin: 0 auto;
			.dingMi-cont-list{
				display:block;
				height:16rem;
				margin-top:1rem;
				border: 1px solid #D9D9D9;
				border-radius:3px;
				padding:.75rem;
				box-sizing:border-box;
				overflow:hidden;
				zoom: 1;
				dl{
					margin-top:.5rem;
					dt{
						font-size:16px;
						font-weight:600;
						color:#333333;
					}
					dd{
						margin-top:.75rem;
						line-height:17px;
						color:#666666;
						font-size:13px;
					}
				}
				.lookDetails{
					height:2.5rem;
					margin-top:1rem;
					border-top:1px solid #E4E4E4;
					line-height:2.5rem;
					color:#666666;
					font-size: 16px;
					span{
						&:nth-of-type(2){
							display:inline-block;
							float:right;
							height:100%;
							width:.5rem;
							line-height:2.5rem;
							color:#CACACA;
						}
					}
				}
			}


		}
		.dingMi-footer{
			width:100%;
			height:41px;
			position:fixed;
			bottom:0;
			left:0;
			font-size: 16px;
			.clickBlock{
				width:41px;
				height:41px;
				position:fixed;
				bottom:0;
				left:0;
				z-index:900;
			}
			.checkFooterDiv{
				width:100%;
				height:100%;
				position:relative;
				.showOne,.showTwo{
					display:flex;
					margin-top:50px;
					-webkit-animation:myadd 500ms;
            		animation-fill-mode: forwards;
				}
				@keyframes myadd{
		            from{margin-top:50px;}
		            to{margin-top:0px;}
        		}

			}

		}

	}

</style>



